import React, { Fragment } from 'react';
import '../CSS/boxes.css'

const SubmitText = () => {
    return (
        <div className="mb-0">
            <div className="h-align-center mt-1">
                <p className="i-tittle">Share your thoughts</p>
            </div>
            <form className="box-i my-auto mt-s0">
                <textarea className="box-i bg-secondary" maxLength={500} name="Text-input">
                </textarea>
                <button className="btn-primary i-tittle">Send</button>
            </form>
        </div>
    );
}

export default SubmitText;